  -webkit-transition: all 500ms;
  transition: all 500ms;
  height: 100%;
  top: 46px;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
